
/* eslint-disable indent,camelcase,prefer-promise-reject-errors */

import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import Picker from 'vux/src/components/picker/index.vue'

import UtilMixin from '@/mixins/UtilMixin.vue'

export default {
mixins: [UtilMixin],
data() {
return {
groupColor: '#50b97b',
partnerId: 'yousu',
loginType: null,
mobile: null,
name: null,
certNo: null,
cityCode: null,
cityList: []
}
},
components: {
XInput,
Group,
XButton,
Box,
XAddress,
Picker
},
computed: {
picker() {
const data = this.data || []
let filterData = []
data.map(item => {
if (item.status === '0') {
filterData.push(item)
}
})
return filterData
}
},
methods: {
formMixin_submit(ajaxUrl) {

const ISEMPTY = 0
const ISNOVALID = 1
return new Promise((resolve, reject) => {
let params = {}
for (let key in this.$refs) {
let ref = this.$refs[key]
if (ref.required && !ref.currentValue) {
this.$vux.toast.text('请将信息填写完整', 'top')
ref.focus()
reject({
code: ISEMPTY,
msg: '信息填写不完整'
})
return
} else {
if (!ref.valid) {
this.$vux.toast.text(ref.firstError, 'top')
ref.focus()
reject({
code: ISNOVALID,
msg: '信息填写不完整'
})
return
}
}
if (ref.name) {
params[ref.name] = ref.currentValue
}
}
resolve(params)
})
},
submit() {
this.formMixin_submit('/bqs_api/')
.then((res) => {
this.$router.push({
name: 'ApiGJJAuthNext',
query: {baseInfo: JSON.stringify(res), checkway: this.$route.query.checkway}
})
})
},
ajaxCityList() {
this.$axios._post({
url: '/bqs_api/',
data: {
url: 'https://credit.baiqishi.com/clweb/api/hfund/supportcity',
data: {
partnerId: this.partnerId
}
}
}).then(res => {
this.data = res.cityInfoList
const cityList = res.cityInfoList
let cityData = []
cityList.forEach((item) => {
if (item.status === '0') {
cityData.push({
name: item.cityCodeDesc ? item.cityCodeDesc : item.provinceDesc,
value: item.cityCode
})
}
})
this.cityList = cityData
})
},
changeCity(ids, names) {
console.log(ids)
console.log(names)
this.cityCode = ids[ids.length - 1]
this.ajaxLoginType()
},
ajaxLoginType() {
this.$axios._post({
url: '/bqs_api/',
data: {
url: 'https://credit.baiqishi.com/clweb/api/hfund/logintype',
data: {
partnerId: this.partnerId,
cityCode: this.cityCode
}
}
}).then(res => {
this.loginType = res.loginCmdFields[0].loginType
})
}
},
created() {
this.ajaxCityList()
},
mounted() {
}
}



